*{
    padding: 0;
    margin: 0;
}
:root{
    --font-1: 'Satisfy', cursive;
    --font-2: 'Poppins', sans-serif;
    --font-3: 'Protest Revolution', sans-serif;
    --color-1: #F8F4E1;
    --color-2: #FED8B1;
    --color-3: #543310;
    --color-4: #A67B5B;

}
html {
    scroll-behavior: smooth;
  }
@media screen and (max-width: 480px){
    .product-container{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        padding: 5rem 0;
        background-image: url('./bg.webp');
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
        max-width: 100%;
    }
    .product{
        max-width: 100%;
        padding-bottom: 2rem;
    }
    .list img{
        height: 300px;
        width: 100%;
    }
/**************************************************/
    /*---MACARONS---*/
    .macarons-container{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    .macarons-container h1{
        font-family: var(--font-3);
        font-weight: 400;
        font-style: normal;
        font-size: 3rem;
        text-align: center;
        color: var(--color-4);
    }
    .macarons-list{
        width: 50vw;
    }
    .item{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        gap: 1rem;
    }
    .item p{
        font-family: var(--font-2);
        font-weight: 500;
        font-size: 1rem;
        font-style: normal;
        text-align: center;
    }
    .macarons-content{
        background-image: url('./macarons.webp');
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
        background-attachment: fixed;
        max-width: 100%;
        min-height: 50vh;
        display: flex;
        justify-content: flex-start;
        align-items: flex-start;
    }
    /*---COOKIES---*/
    .cookies-container{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    .cookies-container h1{
        font-family: var(--font-3);
        font-weight: 400;
        font-style: normal;
        font-size: 3rem;
        text-align: center;
        color: var(--color-4);
    }
    .cookies-list{
        width: 50vw;
    }
    .item{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        gap: 1rem;
    }
    .item p{
        font-family: var(--font-2);
        font-weight: 500;
        font-size: 1rem;
        font-style: normal;
        text-align: center;
    }
    .cookies-content{
        background-image: url('./cookies.webp');
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
        background-attachment: fixed;
        max-width: 100%;
        min-height: 50dvh;
        display: flex;
        justify-content: flex-end;
    }
    /*---CUPCAKES---*/
    .cupcakes-container{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    .cupcakes-container h1{
        font-family: var(--font-3);
        font-weight: 400;
        font-style: normal;
        font-size: 3rem;
        text-align: center;
        color: var(--color-4);
    }
    .cupcakes-list{
        width: 50vw;
    }
    .item{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        gap: 1rem;
    }
    .item p{
        font-family: var(--font-2);
        font-weight: 500;
        font-size: 1rem;
        font-style: normal;
        text-align: center;
    }
    .cupcakes-content{
        background-image: url('cupcake.webp');
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
        background-attachment: fixed;
        max-width: 100%;
        min-height: 50dvh;
        display: flex;
        justify-content: flex-end;
    }
/**********************************************/
    .content{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        gap: 2rem;
        padding: 5rem 0;
        min-height: 50vh;
        max-width: 100%;
        padding: 5rem 0;
        background-image: url('./bg.webp');
    }
    .content h1{
        font-family: var(--font-3);
        font-weight: 400;
        font-style: normal;
        font-size: 2.2rem;
        text-align: center;
        color: var(--color-4);
        padding: 0 1rem;
    }
    .content a{
        font-family: var(--font-2);
        font-weight: 500;
        font-style: normal;
        font-size: 1rem;
        color: var(--color-3);
        padding: .5rem 2.5rem;
        border: 3px solid var(--color-3);
        border-radius: 5px;
        cursor: pointer;
    }
    .content a:hover{
        background-color: var(--color-1);
    }
}

